import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import CountryView from '../views/CountryView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/country/:country_id',
    name: 'Country',
    component: CountryView,
    props: true,
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router
